<template>
    <div class="account-name-doEdit">
        <mt-field label="账单号:"
                  readonly
                  disableClear
                  v-model="infoData.bill_No"
                  type="text"></mt-field>
        <mt-field label="收款人:"
                  readonly
                  disableClear
                  v-model="infoData.Payee_name"
                  type="text"></mt-field>
        <mt-field label="交易币种:"
                  readonly
                  disableClear
                  v-model="infoData.account_money_type"
                  type="text"></mt-field>
        <mt-field label="账单号:"
                  readonly
                  disableClear
                  v-model="infoData.account_money"
                  type="text"></mt-field>
        <mt-field label="收款人:"
                  readonly
                  disableClear
                  v-model="infoData.account_date"
                  type="text"></mt-field>
        <mt-field label="交易币种:"
                  readonly
                  disableClear
                  v-model="infoData.account_info"
                  type="text"></mt-field>
    
        <div class="field-action"
             v-if="!isPay && !isRefuse">
            <mt-button type="primary"
                       size="small"
                       @click="doPay">支付</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doRefuse">拒绝</mt-button>
        </div>
        <div v-if="isRefuse"
             class="field-part">
            <mt-field label="拒绝理由:"
                      v-model="refuse"
                      type="textarea"
                      rows="4"></mt-field>
            <div class="field-action">
                <mt-button type="primary"
                           size="small"
                           @click="doBack('isRefuse')">上一步</mt-button>
                <mt-button type="primary"
                           size="small"
                           @click="doConfirm('refuse')">确定</mt-button>
                <mt-button type="primary"
                           size="small"
                           @click="$router.go(-2)">返回</mt-button>
            </div>
        </div>
        <div v-if="isPay">
            <div class="field-part">
                <mt-cell title="选择支付账户"
                         v-model="dataForm.tranAccount"
                         @click.native=" showAccountSheet = true "
                         is-link></mt-cell>
                <mt-field label="交易密码:"
                          v-model="dataForm.tranAccountPwd"
                          type="text"></mt-field>
                <mt-field label="短信验证码:"
                          disableClear
                          v-model="dataForm.tranAccountMsg"
                          type="text">
                    <div class="field-addon-btn">
                        <mt-button type="primary"
                                   size="small"
                                   plain
                                   :disabled="codeStatus"
                                   ref="code"
                                   @click="doMsg">{{codeMsg}}</mt-button>
                    </div>
                </mt-field>
            </div>
            <div class="field-action">
                <mt-button type="primary"
                           size="small"
                           @click="doBack('isPay')">上一步</mt-button>
                <mt-button type="primary"
                           size="small"
                           @click="doConfirm('pay')">确定</mt-button>
                <mt-button type="primary"
                           size="small"
                           @click="$router.go(-2)">返回</mt-button>
            </div>
            <!--    账户弹出框    -->
            <mt-actionsheet :actions="accountListSheet"
                            v-model="showAccountSheet"></mt-actionsheet>
        </div>
    
    </div>
</template>

<script>
//获取一卡通账户
import * as serviceAccount from '../../services/account';
export default {
    name: 'account-name-doEdit',
    data() {
        return {
            infoData: {
                bill_No: '',
                Payee_name: '',
                account_money_type: '',
                account_money: '',
                account_date: '',
                account_info: ''

            },
            dataForm: {
                tranAccount: '',
                tranAccountId: '',
                tranAccountPwd: '',
                tranAccountMsg: ''
            },
            showAccountSheet: false,
            accountListSheet: [],
            isPay: false,
            isRefuse: false,
            codeStatus: false,  //验证码按钮是否disabled
            refuse: '',
            code: '',
            codeMsg: '获取验证码'
        };
    },
    methods: {
        doPay() {
            this.getAccount();  //获取弹框账户内容
            this.isPay = true;
        },
        doRefuse() {
            this.isRefuse = true;
        },
        doBack(type) {
            this[type] = false;
        },
        doConfirm(type) {
            if (type === "pay") {
                let _data = Object.assign({}, this.dataForm);
                if (this.$validate.isEmpty(_data.tranAccountPwd)) {
                    this.$msg('交易密码不能为空。', 'error', 1000);
                    return false;
                }
                if (!this.$validate.isPwd(_data.tranAccountPwd)) {
                    this.$msg('交易密码格式错误。', 'error', 1000);
                    return false;
                }
                if (this.$validate.isEmpty(_data.tranAccountMsg)) {
                    this.$msg('验证码不能为空。', 'error', 1000);
                    return false;
                }
                if (_data.tranAccountMsg !== this.code) {
                    this.$msg('验证码输入错误。', 'error', 1000);
                    return false;
                }
            }
            else {
                if (this.$validate.isEmpty(this.refuse)) {
                    this.$msg('拒绝理由不能为空。', 'error', 1000);
                    return false;
                }
            }
            this.$router.push({ name: 'billPayMsg' })
        },
        doMsg() {
            this.$getCode(6, (code) => {
                alert(code);
                this.code = code;
            })
            this.codeStatus = true;
            let second = 10;
            let codeInterval = setInterval(() => {
                if (second > 1) {
                    second -= 1;

                }
                else {
                    this.codeStatus = false;
                    this.codeMsg = "获取验证码";
                    clearInterval(codeInterval);
                    return;
                }
                this.codeMsg = `${second}s后再次获取`;
            }, 1000)
        },
        sheetCallback(item) {
            this.dataForm.tranAccount = item.name;
            this.dataForm.tranAccountId = item.id;
        },
        getAccount() {
            serviceAccount.getAccountList().then((res) => {
                if (!res.data.status) {
                    Toast(res.data.message);
                    return;
                }
                let _firstAccount = res.data.list[0];

                if (!this.dataForm.tranAccount) {
                    this.dataForm.tranAccount = _firstAccount.account;
                    this.dataForm.tranAccountId = _firstAccount.id;
                }

                let _temp = [];

                res.data.list.forEach((item, index) => {
                    _temp.push({
                        id: item.id,
                        name: item.account,
                        method: this.sheetCallback,
                    })
                });
                this.accountListSheet = _temp;

            }, (err) => {
                Toast(err);
            });
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
        this.infoData = Object.assign({}, this.infoData, this.$route.params.info);
    }
};
</script>

<style lang="scss" scoped>
.field-addon-btn {
    position: relative;
    right: 5px;
    top: 5px;
}
</style>


