<template>
    <div id="current-child">
        <mt-navbar class="page-part"
                   v-model="selected">
            <mt-tab-item id="all">全部</mt-tab-item>
            <mt-tab-item id="in">支出</mt-tab-item>
            <mt-tab-item id="out">收入</mt-tab-item>
        </mt-navbar>
    
        <mt-tab-container class="page-part-top"
                          v-model="selected">
            <mt-tab-container-item id="all">
                <mt-loadmore ref="loadmore" :auto-fill="false" :top-method="loadTop" :bottom-method="loadBottom" 
                            @top-status-change="handleTopChange" 
                            @bottom-status-change="handleBottomChange" 
                            :bottom-all-loaded="allLoaded" >
                    <ul class="cell-list">
                        <router-link :to="{ name: 'account-transfer-detail', params: { info: item }}"
                                    tag="li"
                                    append
                                    v-for="item in transferList"
                                    :key="item.serial_number">
                            <div class="cell-warp">
                                <div class="cell-left">
                                    <div class="cell-name">{{item.name}}</div>
                                    <div class="cell-balance">余额:{{item.transfer_balance}}</div>
                                </div>
                                <div class="cell-right">
                                    <div class="cell-money">{{item.transfer_money}}</div>
                                    <div class="cell-date">{{item.transfer_date}}</div>
                                </div>
                            </div>
                            <i class="mint-cell-allow-right"></i>
                        </router-link>
                    </ul>
                </mt-loadmore>
            </mt-tab-container-item>
            <mt-tab-container-item id="in">
                <ul class="cell-list">
                    <router-link :to="{ name: 'account-transfer-detail', params: { info: item }}"
                                 tag="li"
                                 append
                                 v-for="item in transferList"
                                 :key="item.serial_number">
                        <div class="cell-warp">
                            <div class="cell-left">
                                <div class="cell-name">{{item.name}}</div>
                                <div class="cell-balance">余额:{{item.transfer_balance}}</div>
                            </div>
                            <div class="cell-right">
                                <div class="cell-money">{{item.transfer_money}}</div>
                                <div class="cell-date">{{item.transfer_date}}</div>
                            </div>
                        </div>
                        <i class="mint-cell-allow-right"></i>
                    </router-link>
                </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="out">
                <ul class="cell-list">
                    <router-link :to="{ name: 'account-transfer-detail', params: { info: item }}"
                                 tag="li"
                                 append
                                 v-for="item in transferList"
                                 :key="item.serial_number">
                        <div class="cell-warp">
                            <div class="cell-left">
                                <div class="cell-name">{{item.name}}</div>
                                <div class="cell-balance">余额:{{item.transfer_balance}}</div>
                            </div>
                            <div class="cell-right">
                                <div class="cell-money">{{item.transfer_money}}</div>
                                <div class="cell-date">{{item.transfer_date}}</div>
                            </div>
                        </div>
                        <i class="mint-cell-allow-right"></i>
                    </router-link>
                </ul>
            </mt-tab-container-item>
        </mt-tab-container>
    </div>
</template>

<script>
import { Toast, InfiniteScroll } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/transfer-list';
let lsPage = ['/my-account/current-list/detail'];
export default {
    name: 'current-child',
    data() {
        return {
            selected: 'all',
            transferList: [],
            
            topStatus: '',
            bottomStatus: '',
            allLoaded: false,
            wrapperHeight: 0,
            translate: 0,
            moveTranslate: 0
        };
    },
    methods: {
        loadTop() {
            console.log( 'loadTop' );

            setTimeout(()=>{
                this.$refs.loadmore.onTopLoaded();
            }, 2000);
        },
        loadBottom() {
            console.log( 'loadBottom' );
            this.allLoaded = false;
            setTimeout(()=>{
                this.$refs.loadmore.onBottomLoaded();
            }, 2000);
        },
        handleTopChange(status) {
            console.log('handleTopChange:' + status);
            this.topStatus = status;
        },
        handleBottomChange(status) {
            console.log('handleBottomChange:' + status);
            this.bottomStatus = status;
        },

        selectedData(id, data) {
            if (id === "all") {
                this.transferList = data;
            }
            else {
                let _copy_ = data.filter((item) => {
                    return item.status == id;
                });
                this.transferList = _copy_;
            }
        },
        getData() {
            return new Promise((resolve, reject) => {
                serviceAccount.getTansferList().then((res) => {
                    if (!res.data.status) {
                        Toast(res.data.message);
                        return;
                    }
                    resolve(res.data.list);
                }, (err) => {
                    reject(err);
                });
            });
        },
        selectedChange(val, oldVal) {
            this.getData().then((data) => {
                this.selectedData(val, data);
            })
        }
    },
    watch: {
        selected: 'selectedChange'
    },
    created() {
        this.getData().then((data) => {
            this.selectedData(this.selected, data);
            this.$store.dispatch('showLoading', false);
        }, (data) => {
            Toast(data);
        });
    },
    mounted() {
        // this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top + 120;
    },
    beforeRouteEnter(to, from, next) {
        next((vm) => {
            if (lsPage.indexOf(from.path) > -1) {
                let _temp = vm.$ls.getItem('transfer-detail-selected');
                vm.selected = _temp;
            }
        })
    },
    beforeRouteLeave(to, from, next) {
        if (lsPage.indexOf(to.path) > -1) {
            this.$ls.setItem('transfer-detail-selected', this.selected);
        }
        next();
    }
};
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
$cell-margin: 12px 0px;
$cell-color: #a5a3a3;
$cell-font-size: 14px;

.page-part-top {
    border-top: 12px solid #efefef;
    .cell-list {
        position: relative;
        background-color: #fff;
        box-sizing: border-box;
        color: inherit;
        min-height: 48px;
        display: block;
        overflow: hidden;
        position: relative;
        text-decoration: none;

        li
        {
            position: relative;

            @include mini-border(top, #d9d9d9, after);

            .mint-cell-allow-right
            {
                &:after
                {
                    right: 20px;
                }
            }
        }

        li:last-child {
            @include mini-border(bottom, #d9d9d9, before);
        }
        .cell-warp {
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            box-sizing: border-box;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            font-size: 16px;
            line-height: 1;
            min-height: inherit;
            overflow: hidden;
            padding: 12px 30px;
            padding-right: 50px;
            width: 100%;
            .cell-left {
                -webkit-box-flex: 1;
                -ms-flex: 1;
                flex: 1;
                .cell-name {
                    margin: $cell-margin;
                    vertical-align: middle;
                }
                .cell-balance {
                    color: $cell-color;
                    margin: $cell-margin;
                    font-size: $cell-font-size;
                }
            }
            .cell-right {
                text-align: right;
                .cell-money {
                    margin: $cell-margin;
                }

                .cell-date {
                    color: $cell-color;
                    margin: $cell-margin;
                    font-size: $cell-font-size;
                }
            }
        }
    }
}
</style>


