const api = {
    ctx: '/mock',
    getUrl: function (key) {
        return this.ctx + this[key];
    },
    getHomeIconNavList: '/home/icon-nav/all',
    getHomeIconNavLists: '/home/icon-nav/all-module',
    getPayeeList: '/payee/list',
    getBankList: '/bank/list',
    getAccountList: '/account/list',
    getAccountChildList: '/account-child/list',
    getTansferList: '/transfer/list',
    getElectricityCityList: '/life/electricity/city',
    getElectricityCompanyList: '/life/electricity/company',
    getElectricityExpenseDetail: '/life/electricity/detail',
    getBillPayList: '/bill-pay/list',
    getReserveList: '/no-card/list',
    getCreditAccountList: '/credit/account/list',
    getCreditAccountDetail: '/credit/account/detail'
};

export default api;



