<template>
    <div id="existBill">
        <div class="field-part">
            <mt-cell title="请选择信用卡"
                     v-model=" dataForm.creditAccount "
                     @click.native=" showAccountSheet = true "
                     is-link></mt-cell>

        </div>

        <div class="field-part" v-if="showSore">
            <mt-cell title="姓名" v-model=" dataForm.creditName "></mt-cell>
            <mt-cell title="账号累计总积分" v-model=" dataForm.creditAccount "></mt-cell>
            <mt-cell title="账号可兑换积分" v-model=" dataForm.creditAccount "></mt-cell>
            <mt-cell title="当前新增积分" v-model=" dataForm.creditAccount "></mt-cell>
        </div>

        <div class="field-action">

            <mt-button type="default"  @click="$back()">返回</mt-button>

            <mt-button type="primary"
                       @click="query()">查询积分明细</mt-button>
        </div>

        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet"
                        v-model="showAccountSheet"></mt-actionsheet>

    </div>
</template>
<script>

import { Toast, Actionsheet } from 'mint-ui-rem-dpr';

import { mapGetters, mapActions } from 'vuex';
//获取一卡通账户
import * as serviceCredit from '../../services/credit';

export default {
    name: 'futureBill',
    data() {
        return {
            dataForm: {
                creditAccountId: '',
                creditAccount: '请选择',
                creditName: ''
            },
            accountList: [],
            accountListSheet: [],
            showAccountSheet: false,
            showSore: false,
        }
    },
    methods: {

        query() {
            let account = this.dataForm.creditAccountId;
            if (this.$validate.isEmpty(account)) {
                Toast({
                    message: '信用卡账号必须选择',
                    iconClass: 'icon-toast-error',
                    duration: 1000
                });
                return;
            }

            this.$router.push({ name: 'creditScoreManageDetail', params: { info: this.dataForm }});
        },
        sheetCallback(item) {
            this.dataForm.creditAccount = item.name;
            this.dataForm.creditAccountId = item.id;
            this.dataForm.creditName = item.accountName;
            this.showSore = true;
        }

    },
    created() {

        //信用卡账户列表
        serviceCredit.getCreditAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            this.accountList = res.data.list;

            /*let _firstAccount = res.data.list[0];

            if (!this.dataForm.creditAccount) {
                this.dataForm.creditAccount = _firstAccount.account;
                this.dataForm.creditAccountId = _firstAccount.id;
            }*/

            let _temp = [];

            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    accountName: item.name,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;

        }, (err) => {
            Toast(err);
        });
    }




}
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';

</style>



