<template>
    <div>
        <div class="field-part">
            <mt-cell title="姓名" :value=" dataForm.name "></mt-cell>
            <mt-cell title="信用额度" :value="dataForm.line"> </mt-cell>
            <mt-cell title="可用额度" value="90954.95"></mt-cell>
            <mt-cell title="还款截止日期" value="20190627"></mt-cell>
            <mt-cell title="预借现金额度" value="300000"></mt-cell>
            <mt-cell title="预借现金可用额度" value="300000"></mt-cell>
            <mt-cell title="现金分期额度" value="300000"></mt-cell>
            <mt-cell title="现金分期可用额度" value="300000"></mt-cell>
            <mt-cell title="最低颖缴额" value="8468.56"></mt-cell>
            <mt-cell title="产品级别" value="金卡"></mt-cell>
            <mt-cell title="开户日期" value="20171108"></mt-cell>
            <mt-cell title="账单日" value="02"></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="default"  @click="$back()">返回</mt-button>
        </div>
    </div>


</template>

<script>
    export default {
        data (){
            return {
                dataForm: {
                    name: '',
                    line: '',
                    account: ''
                },
            }
        },
        created() {
            this.dataForm = Object.assign({}, this.dataForm, this.$route.params.info );
            if("625228***0089"==this.dataForm.account) {
                this.dataForm.name="郭振宇";
                this.dataForm.line="20000";
            }else if ("625228***0025"==this.dataForm.account){
                this.dataForm.name="郭振宇2";
                this.dataForm.line="30000";
            }else {
                this.dataForm.name="郭振宇3";
                this.dataForm.line="40000";
            }
        }

    }
</script>

<style lang="scss" scoped>

    @import '../../assets/styles/mixins';

</style>



