const router = new VueRouter({
    routes
});

// 全局路由控制

router.beforeEach((to, from, next) => {
    //console.log( 'beforeEach' );

    let _showBack = true;
    if (to.path === '/' || to.path === '/home') {
        _showBack = false;
    }
    store.dispatch('showBack', _showBack);

    let _menuName = routes.filter((item) => { return item.path === to.path });
    let meta = null;
    if (_menuName.length > 0) {
        meta = _menuName[0].meta;
        store.dispatch('updateHdTitle', meta.title);
        (store.state.hd.isShowHd !== meta.showHd) && store.dispatch('showHd', meta.showHd !== false);
        (store.state.tabbar.isShowTabbar !== meta.showTabbar) && store.dispatch('showTabbar', meta.showTabbar !== false);
        (meta.showLoading !== false) && store.dispatch('showLoading', true);
    }

    next();

    (meta && meta.autoCloseLoading === true) && store.dispatch('showLoading', false);

    //console.log( 'beforeEach next' )

});

router.afterEach((to, from) => {
    //console.log( 'afterEach' );
});


// 全局 挂载 back 方法
Vue.prototype.$back = (() => {
    router.go(-1)
});

export default router;



