<template>
    <div>
        <div class="field-part">
            <mt-cell title="客户信息查询:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="客户风险承受等级:"
                      :value="dataForm.grade"
                      type="text"></mt-cell>
            <mt-cell title="风险等级有效截止日期:"
                      :value="dataForm.date"
                      type="text"></mt-cell>
            <mt-cell title="风险等级评估得分:"
                      :value="dataForm.scroll"
                      type="text"></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doReassessment()">重新评估</mt-button>
        </div>
    </div>
</template>

<script>
import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/account';
export default {
    data() {
        return {
            dataForm: {
                grade: '进取型',
                date: '20200908',
                scroll: '40'
            }
        }
    },
    methods: {
        doReassessment() {
            this.$router.push({name: 'financialProductsReassessment'})
        }
    },
    created() {
        serviceAccount.getAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            this.$store.dispatch('showLoading', false);

        }, (err) => {
            Toast(err);
        });
    }
}

</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
</style>


