import axios from 'axios';

import api from './api';

export function getBillPayList() {
    return new Promise(function (resolve, reject) {
        axios.get(api.getUrl('getBillPayList')).then((res) => {
            resolve(res);
        }, (res) => {
            reject(res);
        }).catch((err) => {
            reject(err);
        });
    });
}




