<template>
    <div>
        <div class="field-part">
            <mt-cell title="收款账户:"
                     v-model=" dataForm.tranAccount "
                     @click.native=" showAccountSheet = true "
                     is-link></mt-cell>
        </div>
        <div class="field-part">
            <mt-field label="付款手机号:"
                      v-model="dataForm.call"
                      type="tel"></mt-field>
            <mt-field label="付款人姓名:"
                      v-model="dataForm.name"
                      type="text"></mt-field>
            <mt-field label="交易币种:"
                      readonly
                      disableClear
                      v-model="dataForm.type"
                      type="text"></mt-field>
            <mt-field label="交易金额:"
                      v-model="dataForm.money"
                      type="number"></mt-field>
            <mt-field label="收款事由:"
                      v-model="dataForm.reason"
                      :attr="{ maxlength: 126 }"
                      type="text"></mt-field>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doConfirm()">确定</mt-button>
        </div>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet"
                        v-model="showAccountSheet"></mt-actionsheet>
    </div>
</template>

<script>
import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/account';
export default {
    data() {
        return {
            dataForm: {
                tranAccount: '',
                tranAccountId: '',
                call: '',
                name: '',
                type: '人民币',
                money: '',
                reason: ''
            },
            accountListSheet: [],
            showAccountSheet: false,
        }
    },
    methods: {
        sheetCallback(item) {
            this.dataForm.tranAccount = item.name;
            this.dataForm.tranAccountId = item.id;
        },

        doConfirm() {
            let _data = Object.assign({}, this.dataForm);
            if (this.$validate.isEmpty(_data.call)) {
                this.$msg('付款手机不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isPhone(_data.call)) {
                this.$msg('手机号码格式不正确。', 'error', 1000);
                return false;
            }
            if (this.$validate.isEmpty(_data.name)) {
                this.$msg('付款姓名不能为空。', 'error', 1000);
                return false;
            }
            if (this.$validate.isEmpty(_data.money)) {
                this.$msg('交易金额不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isAmount(_data.money)) {
                this.$msg('交易金额格式错误。', 'error', 1000);
                return false;
            }
            if (this.$validate.isEmpty(_data.reason)) {
                this.$msg('收款事由不能为空。', 'error', 1000);
                return false;
            }
            this.$router.push('/bill-pay/msg')
        }
    },
    created() {
        serviceAccount.getAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            let _firstAccount = res.data.list[0];

            if (!this.dataForm.tranAccount) {
                this.dataForm.tranAccount = _firstAccount.account;
                this.dataForm.tranAccountId = _firstAccount.id;
                this.dataForm.balance = `${_firstAccount.balance}元`;
            }

            let _temp = [];

            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    balance: item.balance,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;

        }, (err) => {
            Toast(err);
        });
    }
}

</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
.mint-field .mint-cell-title {
    width: 150px!important;
}
</style>


