<template>
    <div>
        <ul class="icon-navs">
            <li class="item"
                v-for=" item in iconNavList ">
                <router-link :to=" item.path ">
                    <i class="iconfont"
                       :class=" item.icon "></i>
                    <cite>{{ item.name }}</cite>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';

import { mapGetters, mapActions } from 'vuex';

import * as serviceHome from '../services/home';

export default {
    data() {
        return {
            iconNavList: []
        };
    },
    created() {
        serviceHome.getHomeIconNavLists().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            let data = res.data.list.filter( (item) => {
                return item.status === "all"
            })
            this.iconNavList = data;

            this.$store.dispatch('showLoading', false);

        }, (err) => {
            Toast(err);
        });
    }
};
</script>



<style lang="scss" scoped>
@import '../assets/styles/mixins';

.icon-navs {
    @include clearfix();

    .item {
        float: left;
        width: 25%;
        a {
            display: block;
            padding: 0.4em 0px 0.4em 0px;
            text-align: center;
            min-height: 50px;

            &:active {
                background-color: #f2f2f2;
            }

            .iconfont {
                display: block;
                height: 60px;
                line-height: 60px;
                font-size: 36px;
                color: #93C23C;
            }

            cite {
                display: block;
                color: #333;
                font-size: 12px;
            }
        }
    }
}
</style>



