import axios from 'axios';

import api from './api';

//获取信用卡账户列表
export function getCreditAccountList( data ) {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getCreditAccountList'), data).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}


//获取信用卡账户详细信息
export function getCreditAccountDetail( data ) {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getCreditAccountDetail'), data).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}



