<template>
    <div class="account-name-editResult">
        <i class="iconfont icon-toast-success "></i>
        <cite>账户别名设置成功！</cite>
        <div class="field-action">
            <mt-button type="primary"
                       size="large"
                       @click="to">返回</mt-button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'account-name-editResult',
    methods: {
        to() {
            this.$router.go(-3);
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
    }
};
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
.account-name-editResult {
    padding: 60px 30px;
    text-align: center;

    .iconfont {
        font-size: 48px;
        color: #5CB85C;
    }
    cite {
        display: block;
        font-size: 16px;
        line-height: 2em;
        color: #5CB85C;
    }

    .mint-button {
        margin-top: 50px;
    }
}
</style>


