<template>
    <div>
        <div class="cell-msg-warp">
            <div class="cell-msg-title">提示信息：</div>
            <div class="cell-msg-content">
                <i class="iconfont icon-toast-success "></i>
                <cite>成功！(此处提示信息由ajax获取)</cite>
            </div>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="large"
                       @click="$router.go($route.params.go)">返回</mt-button>
        </div>
    </div>
</template>

<script>

export default {
    created() {
        this.$store.dispatch('showLoading', false);
    }
}

</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
.cell-msg-warp {
    margin: 20px 10px;
    .cell-msg-title {
        font-size: 16px;
    }
    .cell-msg-content {
        margin: 15% auto;
        text-align: center;
        .iconfont {
            font-size: 48px;
            color: #26a2ff;
        }
        cite {
            display: block;
            font-size: 16px;
            line-height: 2em;
        }
    }
}
</style>


