<template>
    <div>
        <div class="field-part">
            <mt-cell title="取款账户:"
                     v-model=" dataForm.tranAccount "
                     @click.native=" showAccountSheet = true "
                     is-link></mt-cell>
            <mt-field label="账户余额:"
                      readonly
                      disableClear
                      v-model="dataForm.balance"
                      type="text"></mt-field>
            <mt-field label="预约金额:"
                      placeholder="请输入100的整数倍"
                      v-model="dataForm.reserve_money"
                      type="number"></mt-field>
            <mt-field label="交易密码:"
                      v-model="dataForm.password"
                      :attr="{ minlength: 6, maxlength: 6 }"
                      type="text"></mt-field>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doReserve()">预约</mt-button>
        </div>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet"
                        v-model="showAccountSheet"></mt-actionsheet>
    </div>
</template>

<script>
import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/account';
export default {
    data() {
        return {
            dataForm: {
                tranAccount: '',
                tranAccountId: '',
                balance: '',
                reserve_money: '',
                password: ''

            },
            accountListSheet: [],
            showAccountSheet: false,
        }
    },
    methods: {
        sheetCallback(item) {
            this.dataForm.tranAccount = item.name;
            this.dataForm.tranAccountId = item.id;
            this.dataForm.balance = `${item.balance}元`;
        },

        doReserve() {
            let _data = Object.assign({}, this.dataForm);
            let _remainder = parseFloat(_data.reserve_money) % 100;
            if (this.$validate.isEmpty(_data.reserve_money)) {
                this.$msg('预约金额不能为空。', 'error', 1000);
                return false;
            }
            if (_data.reserve_money > parseInt(_data.balance)) {
                this.$msg('预约金额不能大于账户余额', 'error', 1000);
                return false;
            }

            if (_remainder !== 0) {
                this.$msg('请输入100的整数倍', 'error', 1000);
                return false;
            }

            if (this.$validate.isEmpty(_data.password)) {
                this.$msg('交易密码不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isPwd(_data.password)) {
                this.$msg('交易密码格式错误。', 'error', 1000);
                return false;
            }
            this.$router.push({ name: 'noCardMsg', params: { go: '-2' } });
        }
    },
    created() {
        serviceAccount.getAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            let _firstAccount = res.data.list[0];

            if (!this.dataForm.tranAccount) {
                this.dataForm.tranAccount = _firstAccount.account;
                this.dataForm.tranAccountId = _firstAccount.id;
                this.dataForm.balance = `${_firstAccount.balance}元`;
            }

            let _temp = [];

            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    balance: item.balance,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;

        }, (err) => {
            Toast(err);
        });
    }
}

</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
</style>


