<template>
    <div>
        <div>
            <mt-cell  v-for=" (item, index) in accountList"  is-link key="index" :to="{ name: 'creditCardAccountDetail' , params: { info: item }}">
                <span slot="title"> {{item.account}}/{{item.name}} </span>
            </mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="default"  @click="$back()">返回</mt-button>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';

    import * as serviceCredit from '../../services/credit';

    export default {
        data (){
            return {
                accountList: [
                ],
            }
        },
        created() {
            serviceCredit.getCreditAccountList().then((res)=> {
                if(!res.data.status){
                    Toast(res.data.message);
                    return;
                }
                this.accountList = res.data.list;

            },(err)=>{
                Toast(err);
            });
        }
    }
</script>

<style lang="scss" scoped>

    @import '../../assets/styles/mixins';

</style>



