<template>
    <div class="account-name-doEdit">
        <mt-field label="取款账号:"
                  readonly
                  disableClear
                  v-model="infoData.account"
                  type="text"></mt-field>
        <mt-field label="取款编号:"
                  readonly
                  disableClear
                  v-model="infoData.list"
                  type="text"></mt-field>
        <mt-field label="预约金额:"
                  readonly
                  disableClear
                  v-model="infoData.money"
                  type="text"></mt-field>
        <mt-field label="预约日期:"
                  readonly
                  disableClear
                  v-model="infoData.date"
                  type="text"></mt-field>
        <mt-field label="预约状态:"
                  readonly
                  disableClear
                  v-model="infoData.status"
                  type="text"></mt-field>
        <mt-field label="温馨提示:"
                  readonly
                  disableClear
                  v-model="infoData.info"
                  type="textarea"></mt-field>
    
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       v-if="flag"
                       @click="doCancel">取消预约</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
        </div>
    
    </div>
    
    </div>
</template>

<script>
//获取一卡通账户
import * as serviceAccount from '../../services/account';
export default {
    name: 'account-name-doEdit',
    data() {
        return {
            infoData: {
                account: '',
                name: '',
                money: '',
                list: '',
                date: '',
                status: '',
                info: '仅显示当日及未使用预约记录，最多20条记录'

            }
        };
    },
    computed: {
        flag() {
            return this.infoData.status === "未使用" ? true : false
        }
    },
    methods: {
        doCancel() {
            this.$router.push({ name: 'noCardMsg', params: { go: '-4' } });
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
        this.infoData = Object.assign({}, this.infoData, this.$route.params.info);
    }
};
</script>



