<template>
    <div id="existBill">
        <div class="field-part">
            <mt-cell title="请选择信用卡"
                     v-model=" dataForm.creditAccount "
                     @click.native=" showAccountSheet = true "
                     is-link></mt-cell>

            <mt-cell title="账单年月"
                     v-model=" dataForm.month "
                     @click.native=" showMonthSheet = true "
                     is-link></mt-cell>
        </div>


        <div class="field-action">

            <mt-button type="default"  @click="$back()">返回</mt-button>

            <mt-button type="primary"
                       @click="query()">查询</mt-button>
        </div>

        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet"
                        v-model="showAccountSheet"></mt-actionsheet>
        <!--    月份弹出框    -->
        <mt-actionsheet :actions="monthListSheet"
                        v-model="showMonthSheet"></mt-actionsheet>

    </div>
</template>
<script>

import { Toast, Actionsheet } from 'mint-ui-rem-dpr';

import { mapGetters, mapActions } from 'vuex';
//获取一卡通账户
import * as serviceCredit from '../../services/credit';

export default {
    name: 'existBill',
    data() {
        return {
            dataForm: {
                month: "",
                creditAccountId: '',
                creditAccount: ''
            },
            accountList: [],
            accountListSheet: [],
            monthListSheet: [],
            showAccountSheet: false,
            showMonthSheet: false
        }
    },
    methods: {

        query() {
            this.$router.push({ name: 'creditExistBillDetail', params: { info: this.dataForm }});
        },
        sheetCallback(item) {
            this.dataForm.creditAccount = item.name;
            this.dataForm.creditAccountId = item.id;
        },
        monthCallback(item) {
            this.dataForm.month = item.name;
        },

    },
    created() {
        let today = new Date();//获得当前日期
        let monthList = [];
        today.setMonth(today.getMonth()+1);

        //月份列表
        for(var i=0 ; i < 6 ;i++){
            today.setMonth(today.getMonth()-1);
            let month = today.getFullYear() + "" ;
            if(Number(today.getMonth())+1 < 10){
                month= month+"0"+(Number(today.getMonth())+1);
            } else {
                month= month+""+(Number(today.getMonth())+1);
            }

            monthList.push({
                id: month,
                name: month,
                method: this.monthCallback,
            })
        }
        let _firstMonth=monthList[0];
        if (!this.dataForm.month) {
            this.dataForm.month = _firstMonth.name;
        }
        this.monthListSheet = monthList;


        //信用卡账户列表
        serviceCredit.getCreditAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            this.accountList = res.data.list;

            let _firstAccount = res.data.list[0];

            if (!this.dataForm.creditAccount) {
                this.dataForm.creditAccount = _firstAccount.account;
                this.dataForm.creditAccountId = _firstAccount.id;
            }

            let _temp = [];

            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;

        }, (err) => {
            Toast(err);
        });
    }




}
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';

</style>



