<template>
    <div id="current">
        <div class="field-part">
            <mt-cell title="请选择信用卡"
                     v-model=" dataForm.creditAccount "
                     @click.native=" showAccountSheet = true "
                     is-link></mt-cell>


            <mt-cell title="开始日期"
                     @click.native="open('startTimePicker')"
                     :value=" dataForm.startDate | date('%Y-%m-%d') "></mt-cell>
            <mt-cell title="结束日期"
                     @click.native="open('endTimePicker')"
                     :value=" dataForm.endDate | date('%Y-%m-%d')"></mt-cell>
        </div>

        <div class="field-action">
            <mt-button type="default"  @click="$back()">返回</mt-button>
            <mt-button type="primary"
                       @click="query()">查询</mt-button>
        </div>
        <!--    开始日期弹出框    -->
        <mt-datetime-picker ref="startTimePicker"
                            type="date"
                            year-format="{value} 年"
                            month-format="{value} 月"
                            date-format="{value} 日"
                            v-model="startPickerDate"
                            @confirm="compareTime('startPickerDate','startDate')"
                            :start-date="initStartDate"
                            :end-date="today" >
        </mt-datetime-picker>
        <!--    结束日期弹出框    -->
        <mt-datetime-picker ref="endTimePicker"
                            type="date"
                            year-format="{value} 年"
                            month-format="{value} 月"
                            date-format="{value} 日"
                            v-model="endPickerDate"
                            @confirm="compareTime('endPickerDate','endDate')"
                            :start-date="initStartDate"
                            :end-date="today" >
        </mt-datetime-picker>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet"
                        v-model="showAccountSheet"></mt-actionsheet>


    </div>
</template>
<script>

import { Toast, Actionsheet } from 'mint-ui-rem-dpr';

import { mapGetters, mapActions } from 'vuex';
//获取一卡通账户
import * as serviceCredit from '../../services/credit';

export default {
    name: 'scoreManageDetail',
    data() {
        return {
            dataForm: {
                startDate: null,
                endDate: null ,
                creditAccountId: '',
                creditAccount: ''
            },
            accountList: [],
            accountListSheet: [],
            showAccountSheet: false,
            initStartDate: null,
            today: new Date(),
            startPickerDate: null,
            endPickerDate: null
        }
    },
    methods: {
        open(picker) {  //弹出时间选择框
            this.$refs[picker].open();
        },
        compareTime(value , type) {     //开始结束日期比较
            let _copy = this.dataForm[type];
            this.dataForm[type] = this[value]
            let beginDate = this.dataForm.startDate;
            let endDate = this.dataForm.endDate;
            if ( endDate < beginDate ) {
                Toast({
                    message: '开始日期不能大于结束日期',
                    position: 'middle',
                    duration: 2000
                })
                this.dataForm[type] = _copy;
            }
        },
        query() {
            let account = this.dataForm.creditAccountId;
            console.log(account);
            if (this.$validate.isEmpty(account)) {
                Toast({
                    message: '信用卡账号必须选择',
                    iconClass: 'icon-toast-error',
                    duration: 1000
                });
                return;
            }

            this.$router.push({ name: 'creditScoreDetailList', params: { info: this.dataForm }});
        },
        timeChange() {
            let lastSixMonth = new Date();
            lastSixMonth.setMonth(lastSixMonth.getMonth() -5 );
            this.dataForm.startDate = lastSixMonth;
            this.initStartDate = lastSixMonth;
            this.dataForm.endDate = this.today;
        },
        sheetCallback(item) {
            this.dataForm.creditAccount = item.name;
            this.dataForm.creditAccountId = item.id;
        },
    },
    created() {

        this.timeChange();

        serviceCredit.getCreditAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            this.accountList = res.data.list;
            let _temp = [];
            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;
        }, (err) => {
            Toast(err);
        });
        this.dataForm = Object.assign({}, this.dataForm, this.$route.params.info );
        this.$store.dispatch('showLoading', false);

    }

}
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
</style>



