<template>
    <div>
        <div class="field-part">
            <mt-cell title="选择账户:"
                     v-model=" dataForm.tranAccount "
                     @click.native=" showAccountSheet = true "
                     is-link></mt-cell>
        </div>
        <div class="field-part">
            <mt-field label="客户名称:"
                      readonly
                      disableClear
                      v-model="dataForm.name"
                      type="text"></mt-field>
            <mt-field label="证件类型:"
                      readonly
                      disableClear
                      v-model="dataForm.type"
                      type="text"></mt-field>
            <mt-field label="证件号码:"
                      readonly
                      disableClear
                      v-model="dataForm.number"
                      type="text"></mt-field>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doQuery()">查询</mt-button>
        </div>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet"
                        v-model="showAccountSheet"></mt-actionsheet>
    </div>
</template>

<script>
import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/account';
export default {
    data() {
        return {
            dataForm: {
                tranAccount: '',
                tranAccountId: '',
                name: '',
                type: '',
                number: ''
            },
            accountListSheet: [],
            showAccountSheet: false,
        }
    },
    methods: {
        sheetCallback(item) {
            this.dataForm.tranAccount = item.name;
            this.dataForm.tranAccountId = item.id;
            this.dataForm.type = item.type;
            this.dataForm.name = item.account_name;
            this.dataForm.number = item.number;
        },

        doQuery() {
            this.$router.push({name: 'financialProductsAssessmentResult'});
        }
    },
    created() {
        serviceAccount.getAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            let _firstAccount = res.data.list[0];

            if (!this.dataForm.tranAccount) {
                this.dataForm.tranAccount = _firstAccount.account;
                this.dataForm.tranAccountId = _firstAccount.id;
                this.dataForm.type =  _firstAccount.certificates_type;
                this.dataForm.name = _firstAccount.account_name_copy;
                this.dataForm.number = _firstAccount.certificates_No;
            }

            let _temp = [];

            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    account_name: item.account_name_copy,
                    type: item.certificates_type,
                    number: item.certificates_No,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;

        }, (err) => {
            Toast(err);
        });
    }
}

</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
</style>


